import {
	ToolbarButton,
	ToolbarGroup,
} from "@wordpress/components";
import {
	MediaUpload,
	MediaUploadCheck,
	BlockControls,
} from "@wordpress/block-editor";
import { __ } from "@wordpress/i18n";

const Controls = ({ attributes, setAttributes }) => {
	const { images } = attributes;

	return (
		<>
			<BlockControls>
				<ToolbarGroup>
					<MediaUploadCheck>
						<MediaUpload
							multiple
							gallery
							addToGallery={true}
							allowedTypes={["image"]}
							value={images.map((img) => img.id)}
							onSelect={(newImages) => {
								const imgData = newImages.map((img) => {
									return {
										id: img.id,
										url: img.url,
										alt: img.alt,
										caption: img.caption,
									};
								});
								setAttributes({ images: imgData });
							}}
							render={({ open }) => (
								<ToolbarButton
									label={__("Edit Images", "random-image-block")}
									icon="edit"
									onClick={open}
								></ToolbarButton>
							)}
						/>
					</MediaUploadCheck>
				</ToolbarGroup>
			</BlockControls>
		</>
	);
};

export default Controls;
