import {
	PanelBody,
	Button,
	ButtonGroup,
	SelectControl,
	ToggleControl,
	__experimentalBoxControl as BoxControl,
	FocalPointPicker,
} from "@wordpress/components";
import {
	MediaUpload,
	MediaUploadCheck,
	InspectorControls,
} from "@wordpress/block-editor";
import { __ } from "@wordpress/i18n";
import ImageSize from "./ImageSize";
import UpgradeMessage from "./helper/UpgradeMessage";

const Inspector = ( props ) => {
	const { attributes, setAttributes } = props;
	const { images, imageSize, imageAlign, imageObjectFit, captionStyle } = attributes;

	// console.log(captionStyle)

	return (
		<InspectorControls>
			<PanelBody title={__("Images", "random-image-block-for-block-editor")}>
				<MediaUploadCheck>
					<MediaUpload
						multiple
						gallery
						addToGallery={true}
						allowedTypes={["image"]}
						value={images.map((img) => img.id)}
						onSelect={(newImages) => {
							const imgData = newImages.map((img) => {
								return {
									id: img.id,
									url: img.url,
									alt: img.alt,
									caption: img.caption,
								};
							});
							setAttributes({ images: imgData });
						}}
						render={({ open }) => {
							return (
								<>
									{
										images ? (
											<>
												<Button
													label={__("Edit Images", "random-image-block-for-block-editor")}
													icon="edit"
													onClick={open}
												>
												{__("Open Gallery", "random-image-block-for-block-editor")}
												</Button>
												<div className="random-image-block__images">
													{
														images.map((img) => {
															return (
																<div className="random-image-block__images__image" key={img.id}>
																	<div className="random-image-block__images__image__action">
																		<Button
																			label={__("Remove Image", "random-image-block-for-block-editor")}
																			icon="trash"
																			onClick={() => {
																				const newImages = images.filter((image) => image.id !== img.id);
																				setAttributes({ images: newImages });
																			}}
																		></Button>
																	</div>
																	<img src={img.url} alt={img.alt} />
																</div>
															);
														})
													}
												</div>
											</>
										) : (
											<Button
												label={__("Select Images", "random-image-block-for-block-editor")}
												icon="edit"
												onClick={open}
											></Button>
										)
									}
								</>
							)
						} }
					/>
				</MediaUploadCheck>
			</PanelBody>
			<PanelBody title={__("Image Setting", "random-image-block-for-block-editor")}>
				<ImageSize
					imageSizes={imageSize}
					onChange={
						(data) => {
							setAttributes({ imageSize: data });
						}
					}
				/>


				{
					! ultraDevsRandomImageBlock.licensing.is_plan_pro ? (
						<>
							<p>Image Object Fit</p>
							<UpgradeMessage />
						</>
					) : (
						<SelectControl
							label={__("Image Object Fit", "random-image-block-for-block-editor")}
							value={imageObjectFit}
							options={[
								{ label: __("", "random-image-block-for-block-editor"), value: "" },
								{ label: __("Fill", "random-image-block-for-block-editor"), value: "fill" },
								{ label: __("Contain", "random-image-block-for-block-editor"), value: "contain" },
								{ label: __("Cover", "random-image-block-for-block-editor"), value: "cover" },
								{ label: __("Scale Down", "random-image-block-for-block-editor"), value: "scale-down" },
								{ label: __("Initial", "random-image-block-for-block-editor"), value: "initial" },
								{ label: __("Inherit", "random-image-block-for-block-editor"), value: "inherit" },
							]}
							onChange={(value) => setAttributes({ imageObjectFit: value })}
						/>
					)
				}
			</PanelBody>
			<PanelBody title={__("Caption Setting", "random-image-block-for-block-editor")}>
				<ButtonGroup className="random-image-block__alignment">
					<p>Image Text Alignment</p>
					<Button
						isPrimary={imageAlign === 'left'}
						isSecondary={imageAlign !== 'left'}
						onClick={() => setAttributes({ imageAlign: 'left' })}
					>
						{__("Left", "random-image-block-for-block-editor")}
					</Button>
					<Button
						isPrimary={imageAlign === 'center'}
						isSecondary={imageAlign !== 'center'}
						onClick={() => setAttributes({ imageAlign: 'center' })}
					>
						{__("Center", "random-image-block-for-block-editor")}
					</Button>
					<Button
						isPrimary={imageAlign === 'right'}
						isSecondary={imageAlign !== 'right'}
						onClick={() => setAttributes({ imageAlign: 'right' })}
					>
						{__("Right", "random-image-block-for-block-editor")}
					</Button>
				</ButtonGroup>
				<SelectControl
					label={__("Show Caption On", "random-image-block-for-block-editor")}
					value={captionStyle.showCaptionOn}
					options={[
						{ label: __("Default", "random-image-block-for-block-editor"), value: "" },
						{ label: __("Hover", "random-image-block-for-block-editor"), value: "hover" },
					]}
					onChange={(showCaptionOn) => setAttributes({ captionStyle: { ...captionStyle, showCaptionOn: showCaptionOn } })}
				/>

				{
					! ultraDevsRandomImageBlock.licensing.is_plan_pro ? (
						<>
							<p>Caption Over Image?</p>
							<UpgradeMessage />
						</>
					) : (
						<>
							<SelectControl
								label={__("Caption Over Image?", "random-image-block-for-block-editor")}
								value={captionStyle.captionOverlay.enabled}
								options={[
									{ label: __("No", "random-image-block-for-block-editor"), value: "no" },
									{ label: __("Yes", "random-image-block-for-block-editor"), value: "yes" },
								]}
								onChange={(value) => setAttributes({ captionStyle: { ...captionStyle, captionOverlay: { ...captionStyle.captionOverlay, enabled: value } } })}
							/>

							{
								"yes" === captionStyle.captionOverlay.enabled && (
									<>
										<FocalPointPicker
											url={images[0]?.url}
											value={captionStyle.captionOverlay.position}
											onChange={(value) => setAttributes({ captionStyle: { ...captionStyle, captionOverlay: { ...captionStyle.captionOverlay, position: value } } })}
										/>
									</>
								)
							}

							{/* <BoxControl
								label={__("Caption Padding", "random-image-block-for-block-editor")}
								values={captionStyle.padding.desktop}
								onChange={(value) => setAttributes({ captionStyle: { ...captionStyle, padding: { ...captionStyle.padding, desktop: value } } })}
							/> */}
						</>
					)
				}
			</PanelBody>
		</InspectorControls>
	);
};

export default Inspector;
