import {
	TabPanel
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import UpgradeMessage from './helper/UpgradeMessage';

const ImageSize = ({ imageSizes, onChange }) => {
	return (
		<div className='udb-image-size'>
			<TabPanel
				className="udb-image-size__tabs"
				activeClass="active-tab"
				tabs={[
					{
						name: 'desktop',
						title: 'Desktop',
						className: 'desktop-tab',
					},
					{
						name: 'tablet',
						title: 'Tablet',
						className: 'tablet-tab',
					},
					{
						name: 'mobile',
						title: 'Mobile',
						className: 'mobile-tab',
					},
				]}
			>
				{
					(tab) => {
						if ( ( tab.name === 'mobile' || tab.name === 'tablet' ) && ! ultraDevsRandomImageBlock.licensing.is_plan_pro ) {
							return (
								<UpgradeMessage />
							)
						}
						return (
							<div className="udb-image-size__tabs__content">
								<div className="udb-image-size__tabs__content__width">
									<label htmlFor="width">Width:</label>
									<input
										type="number"
										id="width"
										value={imageSizes[tab.name].width}
										onChange={(e) => onChange({ ...imageSizes, [tab.name]: { ...imageSizes[tab.name], width: Number( e.target.value ) } })}
									/>
									<select
										value={imageSizes[tab.name].widthUnit}
										onChange={(e) => onChange({ ...imageSizes, [tab.name]: { ...imageSizes[tab.name], widthUnit: e.target.value } })}
									>
										<option value='%'>%</option>
										<option value='px'>px</option>
										<option value='em'>em</option>
										<option value='rem'>rem</option>
										<option value='vw'>vw</option>
									</select>
								</div>

								<div className="udb-image-size__tabs__content__height">
									<label htmlFor="height">Height:</label>
									<input
										type="number"
										id="height"
										value={imageSizes[tab.name].height}
										onChange={(e) => onChange({ ...imageSizes, [tab.name]: { ...imageSizes[tab.name], height: Number( e.target.value ) } })}
									/>
									<select
										value={imageSizes[tab.name].heightUnit}
										onChange={(e) => onChange({ ...imageSizes, [tab.name]: { ...imageSizes[tab.name], heightUnit: e.target.value } })}
									>
										<option value='%'>%</option>
										<option value='px'>px</option>
										<option value='em'>em</option>
										<option value='rem'>rem</option>
										<option value='vh'>vh</option>
									</select>
								</div>
							</div>
						);
					}
				}
			</TabPanel>
		</div>
	);
};

export default ImageSize;
