import { __ } from '@wordpress/i18n';
import { useBlockProps, MediaPlaceholder, BlockIcon } from '@wordpress/block-editor';
import ServerSideRender from '@wordpress/server-side-render';
import './editor.scss';
import Controls from './Controls';
import Inspector from './Inspector';

export default function Edit( props ) {
	const { attributes, setAttributes } = props;
	const {
		images
	} = attributes;

	return (
		<>
		<Controls { ... { attributes, setAttributes } } />
		<Inspector { ... { attributes, setAttributes } } />

		<div {...useBlockProps()}>
			{
				images.length ? (
					<ServerSideRender block="ultradevs/random-image-block" attributes={ attributes } />
				) : (
					<MediaPlaceholder
						multiple
						gallery
						icon={<BlockIcon icon="format-gallery" />}
						allowedTypes={["image"]}
						labels={ {
							title: __( 'Random Image', 'random-image-block' ),
							instructions: __( 'Select Images', 'random-image-block' ),
						} }
						onSelect={ ( newImages ) => {
							const imgData = newImages.map( ( img ) => {
								return {
									id: img.id,
									url: img.url,
									alt: img.alt,
									caption: img.caption,
								}
							} );
							setAttributes( { images: imgData } );
						} }
					/>
				)
			}
		</div>
		</>
	);
}
